import React from "react";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "bootstrap/dist/css/bootstrap.min.css";

function KphComponent() {
  return (
    <div className="mb-4">
      <Row>
        <Col xs={12} className="mb-3">
          <Card>
            <Card.Body>
              <Card.Text>
                <p>
                  By na bieco z prac naszego KPH kliknij{" "}
                  <a
                    href="https://www.facebook.com/profile.php?id=100034776956234"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    tutaj...
                  </a>
                </p>
                <p>
                  Jako Organizacja Przyjaci Harcerstwa (OPH) jestemy czwart
                  gazi organizacyjn ZHP doczajc do Organizacji Harcerzy,
                  Organizacji Harcerek oraz Starszego Harcerstwa.
                </p>
                <p>
                  OPH okrelaj i obowizuj dwa regulaminy: Regulamin Gwny
                  ZHP (dziaajcego poza granicami Kraju)
                </p>
                <p>
                  Regulamin Kola Przyjaci Harcerstwa OPH zrzesza Kola
                  Przyjaci Harcerstwa (KPH) dziaajce przy jednostkach
                  harcerskich na danym terenie.
                </p>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col xs={12} className="mb-3">
          <Card>
            <Card.Body>
              <Card.Title>Cele KPH</Card.Title>
              <Card.Text>
                <p>
                  Celem kadego Kola oprcz propagowania idei harcerstwa jest
                  cisa wsppraca z jednostka lub jednostkami harcerskimi,
                  ktrymi si opiekuje, aby uatwi im wykonywanie zada i
                  programu pracy. Druynom i Gromadom potrzebne jest nasze
                  wsparcie nie tylko moralne, ale i materialne oraz praktyczne:
                  pomagamy przy zakupie nowego sprztu, opacaniu pomieszcze, w
                  ktrych odbywaj si zbirki, dotujemy kolonie, obozy,
                  autokary na wycieczki; organizujemy posiki oraz transport.
                </p>
                <p>
                  Naszym wsparciem finansowym otaczamy rwnie tych, ktrzy
                  bior udzia we wszelkiego rodzaju szkoleniach, a bez ktrych
                  nasze druyny i gromady nie mogyby funkcjonowa.
                </p>
                <p>
                  Nie zapominamy rwnie o naszych stanicach harcerskich,
                  ktrych istnienie jest bardzo istotne dla dziaalnoci naszego
                  Zwizku. Jak kady dom wymagaj one remontw, napraw czy
                  zakupu nowego sprztu. Taki wydatek czsto przerasta
                  moliwoci finansowe stanicy.
                </p>
                <p>
                  Fundusze na nasza dziaalno pochodz nie tylko ze skadek
                  naszych czonkw, ale take (a moe i nawet przede wszystkim)
                  z imprez takich jak zabawy, kiermasze, festyny, aukcje czy
                  loterie. Nasi dziaacze niestrudzenie poszukuj nowych
                  sposobw wypracowywania dochodw wsppracujc ze szkoami i
                  parafiami, na terenie, ktrych istniej.
                </p>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col xs={12} className="mb-3">
          <Card>
            <Card.Body>
              <Card.Title>Czonkowie KPH</Card.Title>
              <Card.Text>
                <p>
                  Czonkiem KPH moe by kady, kto ukoczy 18 lat i kto
                  akceptuje idee harcerstwa zawarte w Przyrzeczeniu i Prawie
                  Harcerskim.
                </p>
                <p>
                  Zachcamy, aby czonkami zostawa kady rodzic skrzata, zucha,
                  harcerki czy harcerza. Zapraszamy rwnie do wsppracy
                  wszystkich sympatykw harcerstwa, ktrzy ceni harcerskie
                  metody wychowawcze.
                </p>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col xs={12} className="mb-3">
          <Card>
            <Card.Body>
              <Card.Title>Czonkowie OPH</Card.Title>
              <Card.Text>
                <p>Czonkowie OPH dziel si na trzy kategorie:</p>
                <ol>
                  <li>Czonkowie KPH </li>
                  <li>
                    Czonkowie wspierajcy (osoby prawne lub fizyczne, ktre
                    wpacaj ustalon skadk, lecz nie bior czynnego udziau w
                    pracy Zwizku)
                  </li>
                  <li>Dziaacze/dziaaczki harcerskie OPH.</li>
                </ol>
                <p>
                  Dziaacze /dziaaczki harcerskie to czonkowie KPH, ktrzy
                  zostali mianowani przez Przewodniczcego ZHP na wniosek
                  Przewodniczcego Okrgu za wieloletnia aktywna prace na
                  odpowiedzialnym stanowisku. [1]
                </p>
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </div>
  );
}

export default KphComponent;
