import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import "bootstrap/dist/css/bootstrap.min.css";

function HomeCardComponent() {
  const currentYear = new Date().getFullYear();
  const yearsRunning = currentYear - 1959;

  return (
    <div className="card-container">
      <Row xs={1} md={2} className="g-2">
        {Array.from({ length: 2 }).map((_, idx) => (
          <Col key={idx}>
            <div className="p-3">
              <Card className="p-3">
                <Card.Body>
                  <Card.Title>
                    {idx === 0 ? "Czuwaj!" : "Aktualnoci"}
                  </Card.Title>
                  <Card.Text>
                    {idx === 0 ? (
                      <>
                        <p>
                          Witamy na stronie smej Londyskiej Druyny Harcerzy.
                          Jestemy dumn druyn harcersk, z ponad{" "}
                          <strong>{yearsRunning}</strong> latami historii, ktra
                          nieustannie rozwija si i przygotowuje na wielkie
                          wyzwania.{" "}
                        </p>
                        <p>
                          W tym roku szczeglnie podekscytowani jestemy
                          nadchodzcym Zlotem wiatowym, ktry odbdzie si w
                          Ameryce. To wydarzenie obiecuje niezapomniane
                          przeycia, moliwo nawizania kontaktw z harcerzami
                          z caego wiata oraz okazj do reprezentowania naszej
                          druyny na midzynarodowej arenie.
                        </p>
                        <p>
                          Ale to nie koniec naszych planw - w przyszoci czeka
                          nas wiele ekscytujcych przygd. Na tej stronie
                          dowiesz si nie tylko o naszej bogatej przeszoci,
                          ale take o tym, co obecnie dzieje si w druynie,
                          naszych pasjach, wyzwaniach i oczekiwaniach na
                          przyszo. Docz do naszej harcerskiej przygody i
                          razem z nami odkrywaj wiat!
                        </p>
                      </>
                    ) : (
                      <>
                        <strong>
                          27/12/23  30/12/23
                          <br />
                          Zimowisko - Paccar Scout Center, Anglia
                        </strong>
                        <br />
                        Lorum ipsum dolor sit amet, consectetur adipiscing elit.
                      </>
                    )}
                  </Card.Text>
                  <Card.Link href="/o-nas">
                    {idx === 0 ? `Wicej o nas` : null}
                  </Card.Link>
                </Card.Body>
              </Card>
            </div>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export default HomeCardComponent;
